import { makeStyles } from '@material-ui/core';
import sceneImg from './scene.jpg'

const useStyles = makeStyles((theme) => {
    return {
        root: {
            height: '100vh',
            // backgroundColor: '#add8e6',
            backgroundImage: `url(${sceneImg})`,
            position: 'relative',
            fontFamily: 'Shadows into light',
        },
        span: {
            display: 'block',
            marginTop: 20,
        },
        cardStyles: {
            position: 'absolute',
            width: '90vw',
            height: '80vh',
            overflow: 'hidden',
            // background: 'linear-gradient(#5077be, #283c5f)',
            backgroundColor: '#add8e6',
            // color: '#d3d3d3',
            fontSize: '12px',
            padding: '20px',
            WebkitBoxSizing: 'border-box',
            MozBoxSizing: 'border-box',
            boxSizing: 'border-box',
        },
        first: {
            position: 'absolute',
            zIndex: -1
        }
    }
})

export default useStyles;