import React from "react";

const Layout = ({ children }) => {
  return (
    <div
      style={{
        position: "relative",
        width: "100%",
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <h1 style={{ position: "absolute",  fontSize: '25px', marginBottom: '10rem' }}>
        Refresh for more ;)<span style={{ display:'block', fontSize: '15px' }}>and stop drinking XD</span>
      </h1>
      <div style={{ position: "absolute",  marginBottom: '70vh', marginRight: '90vw'}}>
        {children}
      </div>
    </div>
  );
};

export default Layout;
