import React from "react";
import Quotes from './Quotes';
import './styles.css';
import useStyles from './useStyles';
import Layout from './Layout';
import { Card } from '@material-ui/core';
import Api from './api';

const App = () => {
    const { root } = useStyles();

    return (
        <div className={root}>
            <Layout>
                <Quotes />
            </Layout>
        </div>
    )
}

export default App;